import { Component, OnInit } from '@angular/core';
import { RolesService, PermissionsService, AlertService } from '../../services/index';

@Component({
    moduleId: module.id,
    templateUrl: 'roles.component.html'
})
export class RolesComponent implements OnInit {

    public loading = false;
    public showList = true;
    public editMode = false;

    public permissionsList: any = [];
    public data: any = [];
    public roles: any = [];
    public form: any = {};
    public filter: any = {};
    public refresh: any;

    constructor(private alertService: AlertService,
        private rolesService: RolesService,
        private permissionsService: PermissionsService) { }

    ngOnInit() {
        this.clearForm();

        this.permissionsService.get()
            .subscribe(
            (res: any) => {
                this.permissionsList = res.data;
            },
            (err) => {
                this.alertService.error('Failed to load permissions list');
            });

        this.loadData();
    }

    saveData(value, valid): void {

        if (this.editMode) {

            this.rolesService.update(this.form, this.form.id)
                .subscribe(
                (res: any) => {
                    this.editMode = false;

                    this.alertService.success('Data Updated');

                    this.clearForm();
                    this.loadData();
                },
                (err) => {
                    this.alertService.error('Failed to Update data');
                }
                );
        } else {
            this.rolesService.save(this.form)
                .subscribe(
                (res: any) => {
                    this.alertService.success('Data Saved');

                    this.clearForm();
                    this.loadData();
                },
                (err) => {
                    this.alertService.error('Failed to save data');
                }
                );
        }

    }

    addData(): void {
        this.showList = false;
        this.editMode = false;
    }

    editData(entry): void {
        this.showList = false;
        this.editMode = true;

        this.form = entry;
    }

    deleteData(entry): void {

        const confirmed = confirm('Are you sure to delete this data?');

        if (confirmed) {
            this.rolesService.delete(entry.id)
                .subscribe(
                (res: any) => {
                    this.loadData();
                },
                (err) => {
                    this.alertService.error('Failed to delete ' + entry.username);
                }
                );
        }
    }

    clearForm(): void {
        window.scroll(0, 0);

        this.form = {
            'id': '',
            'role_name': '',
            'role_description': '',
            'can_delete': '1',
            'login_destination': '/',
            'permissions': []
        };

        this.editMode = false;
        this.showList = true;
    }

    loadData(): void {
        this.loading = true;

        this.rolesService.get()
            .subscribe(
            (res: any) => {
                this.loading = false;

                this.data = res.data;
            },
            (err) => {
                this.loading = false;
                this.alertService.error(err);
            }
            );
    }

    updateSelectedPermissions(event) {
        if (event.target.checked) {
            if (this.form.permissions.indexOf(parseInt(event.target.value, 10)) < 0) {
                this.form.permissions.push(parseInt(event.target.value, 10));
            }
        } else {
            if (this.form.permissions.indexOf(parseInt(event.target.value, 10)) > -1) {
                this.form.permissions.splice(this.form.permissions.indexOf(parseInt(event.target.value, 10)), 1);
            }
        }
    }

    updateProtected(event) {
        if (event.target.checked) {
            this.form.can_delete = 0;
        } else {
            this.form.can_delete = 1;
        }
    }
}
