import { NgModule } from '@angular/core';
import { TabsModule } from 'ngx-bootstrap/tabs';
import { SharedModule } from '../../shared/shared.module';
import { ModalModule } from 'ngx-bootstrap/modal';

import { UsersComponent } from './users.component';
import { RolesComponent } from './roles.component';
import { PasswordComponent } from './change-password.component';
import { SystemRoutingModule } from './system-routing.module';
import { UsersService, RolesService, PermissionsService,
    AlertService } from '../../services/index';

import { SettingComponent } from './settings.component';
import { AngularFileUploaderModule } from 'angular-file-uploader';

@NgModule({
    imports: [
        SharedModule,
        TabsModule.forRoot(),
        ModalModule.forRoot(),
        SystemRoutingModule,
        AngularFileUploaderModule
    ],
    declarations: [UsersComponent, RolesComponent, PasswordComponent, SettingComponent],
    providers: [UsersService, RolesService, PermissionsService,
        AlertService
    ]
})
export class SystemModule { }
