import { Component, OnInit } from '@angular/core';
import { UsersService, RolesService, AlertService } from '../../services/index';

@Component({
    moduleId: module.id,
    templateUrl: 'users.component.html'
})
export class UsersComponent implements OnInit {

    public loading = false;
    public showList = true;
    public editMode = false;
    public search = false;

    public data: any = [];
    public dataLeasing: any = [];
    public filterRoles: any = [];

    public roles: any = [];
    public form: any = {};
    public filter: any = { 'username': '', 'name': '', 'roles_id': '' };
    public refresh: any;

    public dd: any = {
        dataPerusahaan : [],
        dataCabang : [],
        dataCabangall : [],
        dataDivisiPenjualan: []
    };

    public pageSize = 10;
    public currentPage = 1;
    public totalItems = 0;

    constructor(
        private usersService: UsersService,
        private rolesService: RolesService,
        private alertService: AlertService) { }

    ngOnInit() {
        this.clearForm();

        this.rolesService.get()
            .subscribe(
            (res: any) => {
                this.filterRoles = res.data;
            },
            (err) => {
                this.alertService.error(err);
            }
            );


        this.rolesService.get()
            .subscribe(
            (res: any) => {
                this.roles = res.data;
            },
            (err) => {
                this.alertService.error('Failed to load data');
            }
            );

        this.loadData(1);
    }

    saveData(value, valid): void {

        if (this.editMode) {

            this.usersService.update(this.form, this.form.id)
                .subscribe(
                (res: any) => {
                    this.editMode = false;

                    this.alertService.success('Data Updated');

                    this.clearForm();
                    this.loadData(1);
                },
                (err) => {
                    this.alertService.error('Failed to Update data');
                }
                );
        } else {
            this.usersService.save(this.form)
                .subscribe(
                (res: any) => {
                    this.alertService.success('Data Saved');

                    this.clearForm();
                    this.loadData(1);
                },
                (err) => {
                    this.alertService.error('Failed to save data');
                }
                );
        }

    }

    addData(): void {
        this.showList = false;
        this.editMode = false;
    }

    editData(entry): void {
        this.showList = false;
        this.editMode = true;

        this.form = entry;
    }

    deleteData(entry): void {

        const confirmed = confirm('Are you sure to delete this data?');

        if (confirmed) {
            this.usersService.delete(entry.id)
                .subscribe(
                (res: any) => {
                    this.loadData(1);
                },
                (err) => {
                    this.alertService.error('Failed to delete ' + entry.username);
                }
                );
        }
    }

    clearForm(): void {
        window.scroll(0, 0);

        this.form = {
            'id': '',
            'name': '',
            'email': '',
            'username': '',
            'password': '',
            'roles_id': '',
            'cabang_id': '',
            'perusahaan_id': '',
            'divisi_penjualan_id': '',
            'status': '1'
        };

        this.editMode = false;
        this.showList = true;
    }

    doSearch() {
        this.search = true;

        this.loadData(1);
    }

    clearSearch() {

        this.search = false;

        this.data = [];
        this.totalItems = 0;

        this.filter = {
            'username': '',
            'name': '',
            'roles_id': ''

        };

        this.loadData(1);
    }

    loadData(page): void {
        this.loading = true;

        this.usersService.get(this.filter)
            .subscribe(
            (res: any) => {
                this.loading = false;

                this.data = res.data;
            },
            (err) => {
                this.loading = false;
                this.alertService.error(err);
            }
            );
    }
     /**
     * Filter Kota ketika memilih dropdown provinsi
     */
    pilihCabang(onForm) {
        this.form.cabang_id = '';
        this.dd.dataCabang = this.dd.dataCabangall.filter((row) => {
            if (onForm) {
                return row.perusahaan_id === this.form.perusahaan_id;
            } else {
                return row.perusahaan_id === this.filter.perusahaan_id;
            }
        });
    }
}
