import { Component } from '@angular/core';
import { BaseComponent } from './base.component';
import { SettingService, AlertService } from '../../services';

@Component({
    moduleId: module.id,
    templateUrl: 'settings.component.html'
})
export class SettingComponent extends BaseComponent {

    dd: any = {
        'cabang': [],
        'perusahaan': []
    };

    constructor(
        private settingService: SettingService,
        alertService: AlertService) {
        super(alertService);

        this.moduleName = 'QPI Settings';

        this.moduleForm = {
            'id': '',
            'cabang_id': '',
            'perusahaan_id': '',
            'setting_key': '',
            'setting_value': '',
            'setting_description': ''
        };

        this.moduleFilter = {
            'cabang_id': '',
            'perusahaan_id': '',
            'setting_key': ''
        };

        this.modulePermission = 'System.Setting.';

        this.model = settingService;
    }

    loadDependencies() {
       
    }
}
