import { Component, OnInit, ViewChild } from '@angular/core';
import { UsersService, AlertService } from '../../services/index';

@Component({
    moduleId: module.id,
    templateUrl: 'change-password.component.html'
})
export class PasswordComponent implements OnInit {

    @ViewChild('f') htmlForm: HTMLFormElement;

    public loading = false;
    public showList = true;
    public editMode = false;
    public search = false;

    public data: any = [];
    public form: any = {};
    public filter: any = {};
    public refresh: any;

    public pageSize = 10;
    public currentPage = 1;
    public totalItems = 0;

    constructor(private usersService: UsersService, private alertService: AlertService) { }

    ngOnInit() {
        this.clearForm();

    }

    changePassword(): void {

        const confirmed = confirm('Apakah anda yakin mengganti password?');

        if (!confirmed) { return; }

        if (this.loading) { return; }
        this.loading = true;

        this.usersService.password(this.form, this.form.id)
            .subscribe(
                (res: any) => {
                    this.editMode = false;

                    this.alertService.success('Password telah diganti');

                    this.clearForm();
                },
                (err) => {
                    this.loading = false;

                    let errorArr;

                    try {
                        errorArr = JSON.parse(err.error);
                    } catch (e) {
                        errorArr = err.error;
                    }

                    errorArr = errorArr.error;

                    const errMsg = '';

                    Object.keys(errorArr).forEach((fieldName) => {
                        this.htmlForm.form.controls[fieldName].setErrors(errorArr[fieldName][0]);
                    });

                    this.alertService.error('Gagal untuk ganti password');
                }
            );
    }

    clearForm(): void {
        window.scrollTo(0, 0);

        this.form = {
            'password': '',
            'password_confirmation': ''
        };

        this.editMode = false;
        this.showList = true;
    }

}
